.gt-container {
	overflow: auto;
	width: 1000px;
	height: 500px;
}

.gt-left-content {
	display: inline-block;
	width: 20%;
}

.gt-right-content {
	display: inline-block;
	overflow: auto;
	width: 80%;
}

.gt-header div {
	display: inline-block;
	text-align: center;
	outline: 1px solid black;
}
